import React from "react";
import IconButton from "@material-ui/core/IconButton";
import Brightness2RoundedIcon from "@material-ui/icons/Brightness2Rounded";
import WbSunnyRoundedIcon from "@material-ui/icons/WbSunnyRounded";
import Tooltip from "@material-ui/core/Tooltip";
import BodyClassName from "react-body-classname";
import useGenerateTheme from "../../hooks/use-generateTheme";

function ThemeModeToggleButton({ toggleMode }) {
  const { currentTheme, switchToTheme } = useGenerateTheme();

  const handelModeToggle = () => {
    toggleMode(switchToTheme);
  };

  return (
    <BodyClassName className={currentTheme}>
      <Tooltip title={`Switch to ${switchToTheme}`} className="mt-3">
        <IconButton
          aria-label="toggle light / dark mode"
          onClick={handelModeToggle}>
          {currentTheme === "dark" ? (
            <WbSunnyRoundedIcon />
          ) : (
            <Brightness2RoundedIcon />
          )}
        </IconButton>
      </Tooltip>
    </BodyClassName>
  );
}

export default ThemeModeToggleButton;
