import React, { Suspense } from "react";
import "./styles/global.scss";

import Header from "./Components/Navigation/Header";
// import Weather from "./pages/Weather/Weather";
// import Favorites from "./pages/Favorites/Favorites";
import { Paper } from "@material-ui/core";

import { Redirect, Route, Switch } from "react-router-dom";
import { useState } from "react";
import { ThemeProvider } from "@material-ui/styles";
import { createMuiTheme } from "@material-ui/core";
import { BrowserRouter } from "react-router-dom";
import {
  primaryColor,
  primaryDark,
  primaryLight,
  secondaryColor,
  secondaryDark,
} from "./config/config";
import { TransitionGroup, Transition } from "react-transition-group";
import { exit, play } from "./utils/pageTransition";
import Spinner from "./Components/Spinner/Spinner";

const Weather = React.lazy(() => import("./pages/Weather/Weather"));
const Favorites = React.lazy(() => import("./pages/Favorites/Favorites"));

const App = () => {
  const [mode, setMode] = useState("light");

  const theme = createMuiTheme({
    palette: {
      type: `${mode}`,
      primary: {
        main: mode === "light" ? primaryLight : primaryDark,
      },
      secondary: {
        main: mode === "light" ? secondaryColor : secondaryDark,
      },
      background: {
        paper: mode === "dark" ? "black" : primaryColor,
      },
    },
    typography: {
      fontFamily: "Assistant",
    },
  });

  return (
    <ThemeProvider theme={theme}>
      <Paper id="main-paper">
        <BrowserRouter>
          <Header themeMode={mode} onModeToggle={setMode} />
          <Route
            render={({ location }) => {
              const { pathname, key } = location;
              return (
                <TransitionGroup component={null}>
                  <Transition
                    appear={true}
                    onEnter={(node, appear) => play(pathname, node, appear)}
                    onExit={(node) => exit(node)}
                    key={key}
                    timeout={1}>
                    <Suspense fallback={<Spinner text="loading.." />}>
                      <Switch location={location}>
                        <Route exact path="/weather">
                          <Weather />
                        </Route>
                        <Route exact path="/favorites">
                          <Favorites />
                        </Route>
                        <Redirect to="/weather" />
                      </Switch>
                    </Suspense>
                  </Transition>
                </TransitionGroup>
              );
            }}
          />
        </BrowserRouter>
      </Paper>
    </ThemeProvider>
  );
};

export default App;
