import { createSlice } from "@reduxjs/toolkit";
import { BASE_URL } from "../config/config";
import getJSON from "../utils/getJSON";

// import allDataDummy from "./allDataDummy.json";
const initialState = {
  favorites: [],
  mainWeatherData: null,
  alerts: null,
  dataFetching: {
    status: true,
    message: "Getting your default location..",
  },
};
const weatherSlice = createSlice({
  name: "weather",
  initialState,
  reducers: {
    addCityToFavorite: (state) => {
      state.mainWeatherData.isSavedToFav = true;
      state.favorites.push({
        ...state.mainWeatherData,
      });
    },
    removeItemFromFavorites: (state, action) => {
      const index = state.favorites.findIndex(
        (city) => city.Key === action.payload
      );

      if (state.mainWeatherData.Key === action.payload)
        state.mainWeatherData.isSavedToFav = false;

      state.favorites.splice(index, 1);
    },

    setWeatherData: (state, action) => {
      state.mainWeatherData = action.payload;
    },

    setAlert: (state, action) => {
      state.alerts = action.payload;
    },

    clearAlert: (state) => {
      state.alerts = null;
    },
    setDataFetching: (state, action) => {
      state.dataFetching = action.payload;
    },
    resetDataFetching: (state) => {
      state.dataFetching.status = false;
      state.dataFetching.message = "";
    },
  },
});

// פעולות
export const {
  addCityToFavorite,
  removeItemFromFavorites,
  reset,
  setWeatherData,
  setAlert,
  clearAlert,
  setDataFetching,
  resetDataFetching,
} = weatherSlice.actions;
// ערכים

export const favorites = (state) => state.weather.favorites;
export const mainWeatherData = (state) => state.weather.mainWeatherData;
export const currentWeatherInfo = (state) =>
  state.weather.mainWeatherData.currentWeatherInfo;
export const dailyForecasts = (state) =>
  state.weather.mainWeatherData.DailyForecasts;
export const alerts = (state) => state.weather.alerts;
export const dataFetching = (state) => state.weather.dataFetching;

export default weatherSlice.reducer;

// tuhnks
export const handelAlerts = (alert, timeOut = null) => {
  return async (dispatch) => {
    dispatch(setAlert(alert));
    if (!timeOut) return;
    setTimeout(() => {
      dispatch(clearAlert());
    }, timeOut * 1000);
  };
};

export const fetchAllWeatherData = ({ Key, LocalizedName, Country }) => {
  return async (dispatch) => {
    if (!Key || !LocalizedName) return;

    try {
      dispatch(
        setDataFetching({
          status: true,
          message: "Getting The weather for you",
        })
      );
      const result = await Promise.all([
        getJSON(
          `${BASE_URL}/currentconditions/v1/${Key}?apikey=${process.env.REACT_APP_WEATHER_API}`
        ),

        getJSON(
          `${BASE_URL}/forecasts/v1/daily/5day/${Key}?apikey=${process.env.REACT_APP_WEATHER_API}&metric=true `
        ),
      ]);

      const [weatherInfo, forecast] = result;

      const { Temperature, WeatherText, WeatherIcon } = weatherInfo[0];

      const { DailyForecasts } = forecast;

      dispatch(
        setWeatherData({
          cityName: LocalizedName,
          Key,
          country: Country.LocalizedName,
          currentWeatherInfo: {
            Temperature,
            WeatherText,
            WeatherIcon,
          },
          DailyForecasts,
          isSavedToFav: false,
        })
      );
      dispatch(resetDataFetching());
    } catch (err) {
      dispatch(resetDataFetching());
      dispatch(
        handelAlerts({
          message: `Opps! we couldnt find weather data for '${LocalizedName}'. please try again in a few moments`,
          type: "error",
        })
      );
    }
  };
};

export const getDefaultWeatherByLocation = () => {
  return async (dispatch) => {
    if (!navigator.geolocation) return;

    navigator.geolocation.getCurrentPosition(
      async (position) => {
        const { latitude } = position.coords;
        const { longitude } = position.coords;

        try {
          const dataByLocaiton = await getJSON(
            `${BASE_URL}/locations/v1/cities/geoposition/search?apikey=${process.env.REACT_APP_WEATHER_API}&q=${latitude},${longitude} `
          );

          await dispatch(fetchAllWeatherData(dataByLocaiton));
          dispatch(resetDataFetching());
        } catch (error) {
          dispatch(resetDataFetching());
          dispatch(
            handelAlerts({
              message: "couldnt get you default location",
              type: "error",
            })
          );
        }
      },
      (_) => {
        dispatch(resetDataFetching());
        dispatch(
          handelAlerts({
            message: "You didn't approve us to get your location.",
            type: "warning",
          })
        );
      }
    );
  };
};
