import { gsap } from "gsap";

export const play = (pathname, node, appears) => {
  const tl = gsap.timeline({ paused: true, ease: "Power1.easeOut" });
  const delay = appears ? 0 : 0.2;

  if (!node) return;

  if (pathname === "/weather" || pathname === "/") {
    const form = node.querySelector("form");
    const main = node.querySelector("div[class *= 'weather-details']");

    tl.from(form, {
      display: "none",
      y: 100,
      duration: 0.5,
    }).from(
      main,
      {
        display: "none",
        x: 20,

        duration: 0.6,
        delay,
      },
      "-=0.4"
    );
  } else {
    const main = node.querySelector("div[class *= 'favorites']");
    const allFav = document.querySelectorAll(".fav-item");

    if (allFav.length) return;
    tl.from(main, {
      display: "none",
    }).staggerFrom(
      allFav,
      0.175,
      { autoAlpha: 0, x: 25, ease: "Power1.easeOut " },
      0.125
    );
  }

  tl.play();
};

export const exit = (node, pathname) => {
  const tl = gsap.timeline({ paused: true });
  tl.to(node, { autoAlpha: 0, ease: "Power1.easeOut" });
  tl.play();
};
