import "../../../node_modules/react-loader-spinner/dist/loader/css/react-spinner-loader.css";

import classes from "./spinner.module.scss";
import React from "react";
import { CircularProgress } from "@material-ui/core";
import useGenerateTheme from "../../hooks/use-generateTheme";

function Spinner({ text, size }) {
  const { currentTheme } = useGenerateTheme();
  return (
    <div className={classes.spinner}>
      <p className="is-size-3 is-size-6-mobile">{text}</p>
      <CircularProgress
        thickness={5}
        className={`mr-3 ${classes[currentTheme]}`}
        size={size}
        color="primary"
      />
    </div>
  );
}

export default Spinner;
