import { useSelector } from "react-redux";
import { favorites } from "../../store/weather";

import { makeStyles, withStyles } from "@material-ui/core/styles";
import Badge from "@material-ui/core/Badge";
import FavoriteIcon from "@material-ui/icons/Favorite";
import { primaryDark, primaryLight } from "../../config/config";

const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
    },
  },
}));

const StyledBadge = withStyles((theme) => ({
  badge: {
    background: theme.palette.type === "dark" ? primaryDark : primaryLight,
  },
}))(Badge);

function FavoritesItemsCount() {
  const classes = useStyles();
  const numberOfFavoriteItems = useSelector(favorites).length;

  return (
    <span className={classes.root}>
      <StyledBadge
        className={classes.badge}
        badgeContent={numberOfFavoriteItems}>
        <FavoriteIcon />
      </StyledBadge>
    </span>
  );
}

export default FavoritesItemsCount;
