import React from "react";
import { NavLink } from "react-router-dom";

import { ReactComponent as HeroloLogo } from "../../assets/herolo-logo.svg";
import classes from "./nav.module.scss";
import ThemeModeToggleButton from "../ThemeModeButton/ThemeModeToggleButton";
import { withStyles, Paper, useTheme } from "@material-ui/core";
import FavoritesItemsCount from "./FavoritesItemsCount";

const StyledNavBar = withStyles((theme) => ({
  root: {
    boxShadow:
      theme.palette.type === "dark"
        ? "0 4px 1px 1px rgba(255,255,255,0.5)"
        : "0 3px 5px 2px color",
  },
}))(Paper);

const Header = ({ onModeToggle }) => {
  const theme = useTheme();
  const themeType = theme.palette.type;

  return (
    <StyledNavBar
      component="nav"
      elevation={5}
      className={`${classes.navbar}  ${
        themeType === "dark" ? classes["dark-theme"] : classes["light-theme"]
      } `}>
      <div className={`container ${classes.items}`}>
        <HeroloLogo className={classes.logo} />

        <ul>
          <li>
            <NavLink
              className="is-size-4"
              activeClassName={classes.active}
              to="/weather">
              Weather
            </NavLink>
          </li>

          <li className="my-3">
            <NavLink
              className="is-size-4"
              activeClassName={classes.active}
              to="/favorites">
              Favorites
              <FavoritesItemsCount />
            </NavLink>
          </li>
          <li>
            <ThemeModeToggleButton
              themeType={themeType}
              toggleMode={onModeToggle}
            />
          </li>
        </ul>
      </div>
    </StyledNavBar>
  );
};

export default Header;
