import { useTheme } from "@material-ui/core";
import { primaryDark, primaryLight } from "../config/config";

function useGenerateTheme() {
  const theme = useTheme();

  return {
    switchToTheme: theme.palette.type === "dark" ? "light" : "dark",
    currentTheme: theme.palette.type,
    colorBasedTheme: theme.palette.type === "dark" ? primaryDark : primaryLight,
  };
}

export default useGenerateTheme;
